import React from 'react';
import { Component } from 'react';
import Button from 'react-toolbox/lib/button/Button';
import AppBar from 'react-toolbox/lib/app_bar/AppBar';
import Navigation from 'react-toolbox/lib/navigation/Navigation';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import { connect } from 'react-redux';

class MenuComponent extends Component {
  logout = () => {
    document.cookie = "";
    if (window.location == "/users/profile/logout") { window.location.href = "/"; }
  };

  render() {
    return (
      <div id="menu">
        <AppBar theme='appBar' flat>

          <Navigation type='horizontal'>
            <Link to="/webApp/"> </Link>
            <Link to="/webApp/message">  </Link>
            <Link to="/webApp/sections">  </Link>
            <Link to="/webApp/users/profile">  </Link>
            <Link to="/webApp/boats">  </Link>
            <form action="commands/users/profile/logout" method="post">
              <input type="submit" value="" id="link-lookalike" />
            </form>
          </Navigation>
        </AppBar>
      </div>

    );
  }
}
const Menu = connect((state) => state)(MenuComponent);
export default MenuComponent;