import React from 'react';
import { Component } from 'react';
import { connect } from 'react-redux';
class RatingComponent extends Component {

    state = {
        users: []
    };

  
      constructor(props) {
        super(props);
        fetch('commands/users/all', {
            method: 'GET'
        })
            .then((response) => {
                if (!response.ok)
                    this.handleChange('error', ':    .');
                else {
                    response.json().then((resp) => {
                        this.state.users = resp;
                        console.log(resp);
                        this.forceUpdate();
                    }).catch((err) => {
                        console.log(err.message);
                    });
                }
            })
            .catch((error) => {
                alert('   : ' + error.statusText);
            });
    }

    
   
    render() {

        function compare(a,b) {
            if (a.rate > b.rate)
                return -1;
            if (a.rate < b.rate)
                return 1;
            return 0;
            }

        let sortable = [];
        sortable = this.state.users.slice();
        
        sortable = sortable.sort(compare);
        let table = [];
        for (let i = 0; i < sortable.length; i++) {                               {/*  */}         
            console.log(sortable[i]);
            let p = sortable[i];
            table.push (<tr>
                <td>{p.name}</td>                              {/*      json*/}
                <td>{p.rate}</td>
                <td>{p.sportRate}</td>
            </tr>);

        }
               

        return (
            <div id="RaitingComponent">
                <table className="table_blur">
                    <tr>
                        <th></th>
                        <th></th>
                        <th> </th>
                    </tr>
                    {table}
                </table>
            </div>
        );
    }
}

const Rating = connect((state) => state)(RatingComponent);
export default RatingComponent;

