
import React from 'react';
import { Component } from 'react';
import { connect } from 'react-redux';
import Input from 'react-toolbox/lib/input';
import Button from 'react-toolbox/lib/button/Button';

class SignUpComponentToConnect extends Component {
    state = { username: '', password: '', error: '', didSignUp: false };

    handleChange = (name, value) => {
        this.setState({ ...this.state, [name]: value });
    };

    toLogin = () => this.props.dispatch({
        type: 'SET_REGISTRATION',
        value: false
    });

    setError = (msg) => {
        this.handleChange('error', msg);
    };

    signUp = () => {
        if (this.state.username === null || this.state.username.length < 1) {
            this.setError('   .');
            return;
        }
        else {
            if (reg.test(this.state.username) === false  & this.state.username.length > 
            1 & this.state.username != null){
               this.setError('      email');
            return; 
            }
        }
        if (this.state.password === null || this.state.password.length < 1) {
            this.setError('  .');
            return;
        }

        fetch('/users/registration/new', { //    
            method: 'POST',
            body: JSON.stringify({ username: this.state.username, password: this.state.password }) //   
        })
        .then((response) => {
            if (!response.ok)
                this.setError(':    .');
            else {
                response.json().then((resp) => {
                    if (resp.success === "true") {
                        this.setError('');
                        this.handleChange('didSignUp', true);
                    }
                    else {
                        this.setError(resp.error);      // illega exception   
                    }
                }).catch((err) => {
                    this.handleChange('error', err.message);
                });

            }
        })
        .catch((error) => {
           this.setError("   : " + error.statusText);
        });
    };

    renderAfterSignUp() {
        return (
            <div>
                <h2>  </h2>
                <Button onClick={this.toLogin} flat>  </Button>
            </div>
        );
    }

    render() {
        if (this.state.didSignUp)
            return this.renderAfterSignUp();

        return (
            <div>
                <form>
                    <b></b>
                    <Input type='email' label='Email address' id="user"  name="username" value={this.state.username}
                           onChange={this.handleChange.bind(this, 'username')} />
                    <Input type="password" id="pass" label="" name="password" value={this.state.password}
                           onChange={this.handleChange.bind(this, 'password')} />

                    
                    <span style={{color: "red"}}> {this.state.error} </span>
                   
                        <br/>
                    <Button className="button2"  raised primary style={{marginRight: '5px'}} onClick={this.signUp}></Button>
                    <Button className="button2" onClick={this.toLogin} flat></Button>
                </form>
            </div>
        );
    }
}
const reg = /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/;
const SignUpComponent = connect()(SignUpComponentToConnect);
export default SignUpComponent;