import React from 'react';
import { Component } from 'react';
import { connect } from 'react-redux'; 
import { BrowserRouter as Router, Route, Link } from "react-router-dom";


class AllTopicsComponent extends Component {

    state = {
        topics: []
    };

  
      constructor(props) {
        super(props);
        let path = window.location.pathname;
        path = path.split('/');
        
        path = path[3];
        path = "/webApp/commands/forum/" + path + "/all";            {/*  path */}
       
        
        fetch( path, {          
            method: 'GET'
        })
            .then((response) => {
                if (!response.ok)
                    this.handleChange('error', ':    .');
                else {
                    response.json().then((resp) => {
                        this.state.topics = resp;
                        console.log(resp);
                        this.forceUpdate();                       
                    }).catch((err) => {
                        alert(err.message);
                    });
                }
            })
            .catch((error) => {
                alert('   : ' + error.statusText);
            });
    }

    
   
    render() {
        let list = [];
        for (let i = this.state.topics.length -1; i >= 0; i--) {
            console.log(this.state.topics[i]);
            let p = this.state.topics[i].id;
            let sublink = window.location.pathname;
            sublink = sublink.split('/');
            sublink = sublink[3];
            let link = "/webApp/records/"+ sublink + "/" + p.topicName + "/all" ;         
            list.push (
                <li><Link to={link}>{p.topicName}</Link></li>                         
            );

        }
               

        return (
            <div id="SectionsComponent">
                <ol className="pills">
                    {list}
                </ol>
            </div>
        );
    }
}

const Topics = connect((state) => state)(AllTopicsComponent);
export default AllTopicsComponent;