import HeaderComponent from '../Components/HeaderComponent';
import React from 'react';
import { createStore, applyMiddleware } from 'redux';
import { Component } from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import utocka from './utocka.png';
import Menu from '../Components/MenuComponent';
import SomeInfoxImgComponent from '../Components/SomeInfoxImgComponent';
import UserMessage from '../Components/UsertoMessageComponent';

if (window.location.pathname.indexOf('/message') + 1) {
    document.title = "Message Page";
}

function getUsername() {
    let value = "; " + document.cookie;
    let parts = value.split("; " + 'username' + "=");
    if (parts.length === 2)
        return parts.pop().split(";").shift();
    return "username not set";
}

const initialState = { username: getUsername() };

function reducerFunc(state, action) {
    let newState = Object.assign({}, state);
    switch (action.type) {
        case 'SET_USERNAME':
            newState.username = action.username;
            return newState;
        default:
            return newState;
    }
}

const dataStore = createStore(reducerFunc, initialState);

class MessageComponent extends Component {

    render() {
        document.title = "Dialog page";
        return (
            <div>
                <Provider store={dataStore}>
                    <Menu />
                </Provider>
                <SomeInfoxImgComponent info=" ,  )" />
                <div id="main_message">
                    <UserMessage />
                    <div id="footer">
                        <div id="bot">
                            <HeaderComponent author1="@  " author2=" " group=" 2018" />
                        </div>
                    </div>
                </div>
            </div>
        );
    }

}

export default MessageComponent;